<template>
  <div class="container">
    <form class="input-form" @submit="onSubmit" @reset="moveBoardList">
      <div>
        <div class="nickName"> : {{ getNickName }}</div>
      </div>
      <hr />
      <div>
        <label for="title">
          
          <input
            id="title"
            ref="title"
            v-model="board.title"
            type="text"
            placeholder=" ..."
          />
        </label>
      </div>

      <div>
        <textarea
          id="description"
          ref="description"
          v-model="board.description"
          rows="10"
          max-rows="15"
          style="resize: none"
        ></textarea>
      </div>

      <button
        id="btn--regist"
        class="btn btn-simple"
        type="submit"
        variant="primary"
        v-if="type == 'regist'"
      >
        
      </button>
      <button
        id="btn--modify"
        class="btn btn-simple"
        type="submit"
        variant="primary"
        v-else
      >
        
      </button>
      <button id="btn--list" class="btn btn-simple" type="reset"></button>
    </form>
  </div>
</template>

<script>
import boardApi from "@/apis/boardApi.js";
import { mapMutations, mapState } from "vuex";

export default {
  name: "BoardInputForm",
  data() {
    return {
      board: {
        title: "",
        description: "",
        no: "",
      },
      isUserid: false,
    };
  },
  props: {
    type: { type: String },
  },
  async created() {
    // 
    if (this.type === "modify") {
      try {
        const res = await boardApi.get("/?no=" + this.$route.params.no);
        if (res.status == 200) {
          this.board = { ...res.data[0] };
        } else if (res.status == 204) {
          this.$router.push("/board");
          this.SET_DANGER_MSG({
            visible: true,
            msg: "   .",
          });
        } else {
          this.$router.push("/board");
          this.SET_DANGER_MSG({
            visible: true,
            msg: "   .",
          });
        }
      } catch (e) {}
    }
  },
  methods: {
    ...mapMutations("msgStore", [
      "SET_WARNING_MSG",
      "SET_SUCCESS_MSG",
      "SET_DANGER_MSG",
    ]),
    moveBoardList() {
      this.$router.push("/board");
    },
    onSubmit(event) {
      event.preventDefault();

      let err = true;
      let msg = "";
      !this.board.title &&
        ((msg = " "),
        (err = false), //
        this.$refs.title.focus());
      err &&
        !this.board.description &&
        ((msg = " "),
        (err = false),
        this.$refs.description.focus());
      err &&
        !this.board.description.length > 50 &&
        ((msg = " 50  "),
        (err = false),
        this.$refs.description.focus());

      if (!err) {
        this.SET_WARNING_MSG({ visible: true, msg });
      } else {
        this.SET_WARNING_MSG({ visible: false, msg: "" });
        this.type === "regist" ? this.registBoard() : this.modifyBoard();
      }
    },
    async registBoard() {
      const newBoard = {
        title: this.board.title,
        nickName: this.getNickName,
        socialType: this.userInfo.socialType,
        userId: this.userInfo.userId,
        description: this.board.description,
      };
      const res = await boardApi.post("", newBoard);
      if (res.status == 200) {
        this.$router.push("/board");
        this.SET_SUCCESS_MSG({
          visible: true,
          msg: "  !!",
        });
      } else {
        this.SET_DANGER_MSG({
          visible: true,
          msg: "  .  !",
        });
      }
    },
    async modifyBoard() {
      try {
        const res = await boardApi.put("/", {
          title: this.board.title,
          description: this.board.description,
          no: this.board.no,
        });
        this.$router.push("/board");
        if (res.status == 200) {
          this.SET_SUCCESS_MSG({
            visible: true,
            msg: "  .",
          });
        } else {
          this.SET_DANGER_MSG({
            visible: true,
            msg: "   .",
          });
        }
      } catch {}
    },
  },
  computed: {
    ...mapState("userStore", ["userInfo"]),
    getNickName() {
      return this.userInfo.nickName;
    },
  },
};
</script>

