<template>
  <div class="container">
    <div class="card col-sm-12 mt-1">
      <div class="card-body">
        <div class="form-group form-inline justify-content-center">
          <label class="mr-2" for="sido"> : </label>
          <select
            @change="setGugun"
            class="form-control"
            id="sido"
            v-model="sidoData"
          >
            <option :value="null"> </option>
            <option
              v-for="(sido, index) in sidoArr"
              :key="index"
              :value="sido.sidoCode"
            >
              {{ sido.sidoName }}
            </option>
          </select>

          <label class="mr-2 ml-3" for="gugun"> : </label>
          <select
            @change="setDong"
            class="form-control"
            id="gugun"
            v-model="gugunData"
          >
            <option :value="null"> </option>
            <option
              v-for="(gugun, index) in gugunArr"
              :key="index"
              :value="gugun.gugunCode"
            >
              {{ gugun.gugunName }}
            </option>
          </select>

          <label class="mr-2 ml-3" for="dong"> : </label>
          <select
            @change="searchApt"
            class="form-control"
            id="dong"
            v-model="dongData"
          >
            <option :value="null">  </option>
            <option
              v-for="(dong, index) in dongArr"
              :key="index"
              :value="dong.dongCode"
            >
              {{ dong.dongName }}
            </option>
          </select>
        </div>
        <kakao-map :aptList="searchAptList" />
        <table class="table mt-2">
          <colgroup>
            <col width="100" />
            <col width="150" />
            <col width="*" />
            <col width="120" />
            <col width="120" />
          </colgroup>
          <thead>
            <tr>
              <th></th>
              <th
                :class="{ sorted: sortedType == 'aptName' }"
                @click="sort('aptName')"
              >
                
                <img
                  :class="{ descSort: !descSort }"
                  src="/img/up.svg"
                  alt=""
                />
                <img
                  :class="{ descSort: descSort }"
                  src="/img/down.svg"
                  alt=""
                />
              </th>
              <th class="text-center"></th>
              <th
                :class="{ sorted: sortedType == 'buildYear' }"
                @click="sort('buildYear')"
              >
                
                <img
                  :class="{ descSort: !descSort }"
                  src="/img/up.svg"
                  alt=""
                />
                <img
                  :class="{ descSort: descSort }"
                  src="/img/down.svg"
                  alt=""
                />
              </th>
              <th
                :class="{ sorted: sortedType == 'recentPrice' }"
                @click="sort('recentPrice')"
              >
                
                <img
                  :class="{ descSort: !descSort }"
                  src="/img/up.svg"
                  alt=""
                />
                <img
                  :class="{ descSort: descSort }"
                  src="/img/down.svg"
                  alt=""
                />
              </th>
            </tr>
          </thead>
          <!-- <tbody id="searchResult"></tbody> -->
          <tbody>
            <tr
              @click="moveDetail(apt.aptCode)"
              v-for="(apt, ind) in searchAptList"
              :key="ind"
            >
              <td>{{ ind + 1 }}</td>
              <td>{{ apt.aptName }}</td>
              <td class="text-center">
                {{ apt.sidoName }} {{ apt.gugunName }} {{ apt.dongName }}
                {{ apt.jibun }}
              </td>
              <td>{{ apt.buildYear }}</td>
              <td>{{ getWon(apt.recentPrice) }}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
import KakaoMap from "./KakaoMap.vue";
import aptApi from "@/apis/aptApi.js";
import { mapMutations, mapState } from "vuex";
export default {
  components: { KakaoMap },
  data() {
    return {
      sidoArr: null,
      gugunArr: null,
      dongArr: null,
      searchAptList: [],
      sortedType: "aptName",
      descSort: true,
    };
  },
  computed: {
    ...mapState("aptStore", ["sidoCode", "gugunCode", "dongCode"]),
    sidoData: {
      get() {
        return this.sidoCode;
      },
      set(code) {
        this.SET_SIDO_CODE(code);
      },
    },
    gugunData: {
      get() {
        return this.gugunCode;
      },
      set(code) {
        this.SET_GUGUN_CODE(code);
      },
    },
    dongData: {
      get() {
        return this.dongCode;
      },
      set(code) {
        this.SET_DONG_CODE(code);
      },
    },
  },
  async created() {
    const res = await aptApi.get("/sido");
    this.sidoArr = res.data;
    if (!!this.sidoCode) {
      this.setGugun();
    }
    if (!!this.gugunCode) {
      this.setDong();
    }
    if (!!this.dongCode) {
      this.searchApt();
    }
  },
  methods: {
    ...mapMutations("aptStore", [
      "SET_SIDO_CODE",
      "SET_GUGUN_CODE",
      "SET_DONG_CODE",
    ]),
    ...mapMutations("msgStore", ["SET_INFO_MSG", "SET_SUCCESS_MSG"]),
    async setGugun() {
      const res = await aptApi.get("/gugun?sido=" + this.sidoCode);
      this.gugunArr = res.data;
      this.gugunData = this.gugunCode || null;
      this.dongData = this.dongCode || null;
    },
    async setDong() {
      const res = await aptApi.get("/dong?gugun=" + this.gugunCode);
      console.log(res.data);
      const dongs = res.data.filter((apt) => apt.dongName != null);
      this.dongArr = dongs;
      this.dongData = this.dongCode || null;
    },
    async searchApt() {
      const res = await aptApi.get("/apt?dong=" + this.dongCode);
      const apts = res.data.map((apt) => {
        return {
          ...apt,
          recentPrice: +apt.recentPrice.split(",").join(""),
        };
      });
      this.searchAptList = apts;
      if (res.data.length == 0) {
        this.SET_INFO_MSG({ visible: true, msg: "  ." });
      } else {
        this.SET_SUCCESS_MSG({
          visible: true,
          msg: `${res.data.length}   .`,
        });
      }
    },
    moveDetail(code) {
      this.$router.push("/apt/detail/" + code);
    },
    sort(type) {
      if (type == this.sortedType) {
        this.descSort = !this.descSort;
      } else {
        this.descSort = true;
      }
      this.sortedType = type;
      console.log(type, this.sortedType, this.descSort);
      let sortOption = (a, b) => {
        let result = a[type] > b[type] ? 1 : -1;
        if (this.descSort) return result * -1;
        return result;
      };
      this.searchAptList = this.searchAptList.sort(sortOption);
      this.SET_SUCCESS_MSG({
        visible: true,
        msg: `${type} ${
          !this.descSort ? "" : ""
        } .`,
      });
    },
    getWon(price) {
      if (price >= 10000) {
        let uk = Math.floor(price / 10000);
        let chun = price % 10000;
        if (chun == 0) return uk + "";

        return uk + "" + chun + "";
      } else {
        return price + "";
      }
    },
  },
};
</script>

<style lang="scss">
th {
  img {
    display: none;
  }
}
.sorted {
  text-decoration: underline;
  font-weight: 700;
  * {
    display: none;
  }
  .descSort {
    display: inline;
    width: 1em;
    height: 1em;
  }
}
</style>
